const calculerStatutPlan = (plan) => {
  const maintenant = new Date();
  const dateFin = new Date(plan.fin);
  return maintenant >= dateFin ? "Termin" : "En cours";
};

const fetchGainsDisponibles = useCallback(async () => {
    if (contract && signer) {
        try {
            const plansUtilisateur = await contract.obtenirPlansUtilisateur(account); // Rcuprer les plans de l'utilisateur
            const nouveauxGainsParPlan = {};

            plansUtilisateur.forEach((plan, index) => {
                const montant = parseFloat(ethers.utils.formatEther(plan.montant));
                const taux = parseFloat(plan.taux) / 100;
                const dureeEnJours = (Date.now() - plan.debut * 1000) / (1000 * 60 * 60 * 24); // Dure en jours
                const dureeTotale = plan.duree; // Dure totale en jours

                // Calcul des gains pour ce plan
                const gains = montant * taux * Math.min(dureeEnJours / dureeTotale, 1);
                nouveauxGainsParPlan[plan.id] = gains.toFixed(2); // Utilisation de plan.id comme cl
            });

            setGainsParPlan(nouveauxGainsParPlan); // Mettre  jour l'tat des gains
        } catch (error) {
            console.error("Erreur lors de la rcupration des gains disponibles :", error);
        }
    }
}, [contract, signer, account]);

